.button {
  width: 100%;
  padding: 15px;

  color: $font-color-button;
  font-family: $font-button;
  font-size: 17px;
  font-weight: 600;
  line-height: 25px;
  text-transform: uppercase;
  text-align: center;

  border: none;
  background: $button-color;
  border-radius: 8px;
  box-shadow: 0 1px 10px rgba(43, 35, 76, 0.3);
  background-position: center;
  transition: background 0.8s;

  &:hover {
    background: $button-color radial-gradient(circle, transparent 1%, $button-color 1%) center/15000%;
  }

  &:active {
    background-color: rgba(43, 35, 76, 0.3);
    background-size: 100%;
    transition: background 0s;
    outline: none;
  }

  &:focus {
    outline: none;
  }

  body.dark-theme & {
    background-color: $button-color--dark-theme;
    box-shadow: 0 1px 10px rgba(210, 89, 245, 0.6);

    &:hover {
      background: $button-color--dark-theme radial-gradient(circle, transparent 1%, $button-color--dark-theme 1%) center/15000%;
    }

    &:active {
      background-color: rgba(210, 89, 245, 0.6);
      background-size: 100%;
      transition: background 0s;
      outline: none;
    }
  }
}
