.content {
  flex-grow: 0;
  flex-shrink: 0;
  height: 200px;
  display: flex;
  flex-direction: column;

  text-align: center;
}

.content__header {
  margin-bottom: 34px;

  font-weight: 700;
}

.content__link {
  font-family: $font-secondary;
  font-size: 15px;
  font-style: italic;
  line-height: 18px;

  &:hover {
    opacity: 0.87;
  }
}

.content__button {
  margin: 0 auto 24px;
  cursor: pointer;
}
