html {
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;

  font-family: $font-main;
  font-size: 22px;
  font-weight: 400;
  line-height: 28px;
  color: $font-color-main;
  font-style: normal;

  background-color: $bg-color;
  transition: 0.5s;

  &.dark-theme {
    color: $font-color-main--dark-theme;
    background-color: $bg-color--dark-theme;
  }
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  color: $font-color-main;
  text-decoration: none;

  body.dark-theme & {
    color: $font-color-secondary--dark-theme;
  }
}
